.circle {
  width: 25px;
  height: 25px;
  /* background: linear-gradient(to top left, #ffffff, rgb(159, 255, 255)); */
  background: white;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -20px 0 0 -20px;
  pointer-events: none;
  cursor: none;
  z-index: 1000;
}
